import React, { useEffect, useState } from "react";
import requests from "../tmdb";
import axios from "axios";
import Slider from "react-slick";
import "../scss/Banner.scss";
const settings = {
  dots: false,
  infinite: true,
  slidesToShow: 1,
  slidesToScroll: 1,
  autoplay: true,
  autoplaySpeed: 4000,
  // nextArrow: <i class="bx bx-chevron-right"></i>,
  // prevArrow: <i class="bx bx-chevron-right"></i>,
};
export const Banner = () => {
  const [movie, setMovie] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      const respon = await axios.get(requests.nowplayingMoviesUrl);
      const random1 = Math.floor(Math.random() * respon.data.results.length);
      const random2 = Math.floor(Math.random() * respon.data.results.length);
      const random3 = Math.floor(Math.random() * respon.data.results.length);
      setMovie([
        respon.data.results[random1],
        respon.data.results[random2],
        respon.data.results[random3],
      ]);
    };
    fetchData();
  }, []);
  const truncate = (str, n) => {
    return str?.length > n ? str.substr(0, n - 1) + "..." : str;
  };
  return (
    <div className="banners">
      <Slider {...settings}>
        {movie.map((item, index) => (
          <div key={index}>
            <header
              className="banner"
              style={{
                backgroundImage: `url("https://image.tmdb.org/t/p/original/${item.backdrop_path}")`,
                backgroundSize: "cover",
                backgroundRepeat: "no-repeat",
                backgroundPosition: "center",
              }}
            >
              <div className="banner__contents">
                <h1>{item?.title || item?.name || item?.original_name}</h1>
                <p>{truncate(item?.overview, 150)}</p>
                <div className="banner__contents-buttons">
                  <button className="btn btn-primary">Watch</button>
                  <span>+</span>
                </div>
              </div>
              {/* <div className="banner__next">
                <i class="bx bx-chevron-right"></i>
              </div> */}
            </header>
          </div>
        ))}
      </Slider>
    </div>
  );
};
