import React from "react";
import "../scss/SidebarLeftItem.scss";
import { Link } from "react-router-dom";
export const SidebarLeftItem = ({
  title,
  data,
  activeLink,
  handleSetActiveLink,
}) => {
  return (
    <div className="sidebarLeftItem">
      <p className="sidebarLeftItem__title">{title}</p>
      <div className="sidebarLeftItem__content">
        {data.map((item, index) => {
          return (
            <Link
              key={index}
              to={item.route}
              className={
                item.display_name === activeLink
                  ? "sidebarLeftItem__content-link activeLink"
                  : "sidebarLeftItem__content-link"
              }
              onClick={() => {
                handleSetActiveLink(item.display_name);
              }}
            >
              <div className="sidebarLeftItem__content-item">
                <i className={`${item.icon} sidebarLeftItem__content-icon`}></i>
                <span className="sidebarLeftItem__content-title">
                  {item.display_name}
                </span>
              </div>
            </Link>
          );
        })}
      </div>
    </div>
  );
};
