import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import ThemeAction from "../redux/action/ThemeAction";
import "../scss/Setting.scss";
const mode_settings = [
  {
    id: "light",
    name: "Light Mode",
    background: "light-background",
    class: "theme-mode-light",
  },
  {
    id: "dark",
    name: "Dark Mode",
    background: "dark-background",
    class: "theme-mode-dark",
  },
];

const color_settings = [
  {
    id: "blue",
    name: "Blue Color",
    background: "blue-color",
    class: "theme-color-blue",
  },
  {
    id: "red",
    name: "Red Color",
    background: "red-color",
    class: "theme-color-red",
  },
  // {
  //   id: "cyan",
  //   name: "Cyan",
  //   background: "cyan-color",
  //   class: "theme-color-cyan",
  // },
  {
    id: "green",
    name: "Green Color",
    background: "green-color",
    class: "theme-color-green",
  },
  {
    id: "orange",
    name: "Orange Color",
    background: "orange-color",
    class: "theme-color-orange",
  },
];

export const Setting = () => {
  const [currMode, setcurrMode] = useState("light");
  const [currColor, setcurrColor] = useState("blue");
  const dispatch = useDispatch();
  const setMode = (mode) => {
    setcurrMode(mode.id);

    localStorage.setItem("themeMode", mode.class);
    const action = ThemeAction.setMode(mode.class);
    dispatch(action);
  };

  const setColor = (color) => {
    setcurrColor(color.id);

    const action = ThemeAction.setColor(color.class);
    localStorage.setItem("colorMode", color.class);
    dispatch(action);
  };
  useEffect(() => {
    const themeClass = mode_settings.find(
      (e) => e.class === localStorage.getItem("themeMode", "theme-mode-light")
    );

    const colorClass = color_settings.find(
      (e) => e.class === localStorage.getItem("colorMode", "theme-mode-light")
    );

    if (themeClass !== undefined) setcurrMode(themeClass.id);

    if (colorClass !== undefined) setcurrColor(colorClass.id);
  }, []);
  return (
    <div className="setting">
      <p className="setting__title">Setting</p>
      <div className="setting__mode">
        <p className="setting__mode-title">Mode</p>
        <div className="setting__mode-items">
          {mode_settings.map((item, ind) => (
            <div
              key={ind}
              className="item"
              onClick={() => {
                setMode(item);
              }}
            >
              <i className="bx bx-check"></i>
              <div className={`item-color ${item.background}`}></div>
              <p>{item.name}</p>
            </div>
          ))}
        </div>
      </div>
      <div className="setting__color">
        <p className="setting__color-title">Color</p>
        <div className="setting__color-items">
          {color_settings.map((item, ind) => (
            <div
              key={ind}
              className="item"
              onClick={() => {
                setColor(item);
              }}
            >
              <i className="bx bx-check"></i>
              <div className={`item-color ${item.background}`}></div>
              <p>{item.name}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};
