import React, { useEffect } from "react";
import "./App.scss";
import "./scss/themes.scss";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";
import ThemeAction from "./redux/action/ThemeAction";

import { SidebarLeft } from "./components/SidebarLeft";
import { SidebarRight } from "./components/SidebarRight";
import { TvSeries } from "./components/TvSeries";
import { Movies } from "./components/Movies";
import { Animes } from "./components/Animes";
import { TopNav } from "./components/TopNav";
import { Setting } from "./components/Setting";

function App() {
  const themeAction = useSelector((state) => state.ThemeReducer);
  const dispatch = useDispatch();
  useEffect(() => {
    const themeClass = localStorage.getItem("themeMode", "theme-mode-light");

    const colorClass = localStorage.getItem("colorMode", "theme-mode-light");

    dispatch(ThemeAction.setMode(themeClass));

    dispatch(ThemeAction.setColor(colorClass));
  }, [dispatch]);
  return (
    <Router>
      <div className="App">
        <div className={`layout ${themeAction.mode} ${themeAction.color}`}>
          <SidebarLeft />
          <div className="layout__content">
            <TopNav />
            <Switch>
              <Route path="/" exact component={TvSeries} />
              <Route path="/movies" component={Animes} />
              <Route path="/animes" component={Movies} />
              <Route path="/setting" component={Setting} />
            </Switch>
          </div>
          <SidebarRight />
        </div>
      </div>
    </Router>
  );
}

export default App;
