import React from "react";
import "../scss/SidebarRight.scss";
import { SidebarRightItem } from "./SidebarRightItem";
import requests from "../tmdb";
export const SidebarRight = () => {
  return (
    <div className="sidebarRight">
      <div className="sidebarRight__search">
        <input type="text" placeholder="Search" />
        <i class="bx bx-search"></i>
      </div>
      <div className="sidebarRight__content">
        <SidebarRightItem
          title="Popular Movies"
          fetchUrl={requests.populerMoviesUrl}
        />
      </div>
      <button className="sidebarRight__btn">See More</button>
    </div>
  );
};
