import React, { useState } from "react";
import { general__data, menu__data, social__data } from "../assets/SidebarLeft";
import "../scss/SidebarLeft.scss";
import { SidebarLeftItem } from "./SidebarLeftItem";
export const SidebarLeft = () => {
  const [activeLink, setActiveLink] = useState("Home");
  const handleSetActiveLink = (link) => {
    setActiveLink(link);
  };
  return (
    <div className="sidebarLeft">
      <div className="sidebarLeft__logo">
        <i class="bx bxl-spring-boot"></i>
        <span className="logo">Exxmon</span>
      </div>
      <div className="sidebarLeft__content">
        <SidebarLeftItem
          title="MENU"
          data={menu__data}
          activeLink={activeLink}
          handleSetActiveLink={handleSetActiveLink}
        />
        <SidebarLeftItem
          title="SOCIAL"
          data={social__data}
          activeLink={activeLink}
          handleSetActiveLink={handleSetActiveLink}
        />
        <SidebarLeftItem
          title="GENERAL"
          data={general__data}
          activeLink={activeLink}
          handleSetActiveLink={handleSetActiveLink}
        />
      </div>
    </div>
  );
};
