import React from "react";
import "../scss/TvSeries.scss";
import requests from "../tmdb";
import { Row } from "./Row";
import { Banner } from "./Banner";

export const Movies = () => {
  return (
    <div>
      <Banner />
      <Row title="Trending Now" fetchUrl={requests.topRatedUrl} />
      <Row title="Now playing" fetchUrl={requests.nowplayingMoviesUrl} />
      <Row title="Top Rate" fetchUrl={requests.topRatedUrl} />
      <Row title="Action Movies" fetchUrl={requests.actionMoviesUrl} />
      <Row title="Romance Movies" fetchUrl={requests.romanceMoviesUrl} />
      <Row title="Comedy Movies" fetchUrl={requests.comedyMoviesUrl} />
    </div>
  );
};
