import React, { useState } from "react";
import "../scss/TopNav.scss";
import { Link } from "react-router-dom";
import { topnav__data } from "../assets/TopNav";

export const TopNav = () => {
  const [linkActive, setLinkActive] = useState("TV Series");
  const handleActive = (link) => {
    setLinkActive(link);
  };
  return (
    <div className="topnav">
      {topnav__data.map((item, ind) => {
        return (
          <Link
            to={item.path}
            key={ind}
            className={
              item.display_name === linkActive
                ? "topnav__link link-active"
                : "topnav__link"
            }
            onClick={() => {
              handleActive(item.display_name);
            }}
          >
            {item.display_name}
          </Link>
        );
      })}
    </div>
  );
};
