import React, { useState, useEffect } from "react";
import axios from "axios";
import "../scss/Row.scss";
import Slider from "react-slick";
import YouTube from "react-youtube";
import movieTrailer from "movie-trailer";
const opts = {
  height: "390",
  width: "100%",
  playerVars: {
    // https://developers.google.com/youtube/player_parameters
    autoplay: 1,
  },
};
const settings = {
  dots: false,
  infinite: true,
  speed: 500,
  slidesToShow: 4,
  slidesToScroll: 3,
  // nextArrow: <i class="bx bx-chevron-right"></i>,
  // prevArrow: <i class="bx bx-chevron-right"></i>,
};
export const Row = ({ title, fetchUrl }) => {
  const [trailerUrl, setTrailerUrl] = useState("");
  const [movies, setMovies] = useState([]);
  const handleClick = (movie) => {
    if (trailerUrl) {
      setTrailerUrl("");
    } else {
      if (movie?.name) {
        movieTrailer(movie?.name || "")
          .then((url) => {
            const urlParams = new URLSearchParams(new URL(url).search);
            setTrailerUrl(urlParams.get("v"));
          })
          .catch((eror) => console.log(eror));
      } else {
        alert("Sory, We couldn't find the resource");
      }
    }
  };
  useEffect(() => {
    const fetchData = async () => {
      const respon = await axios.get(fetchUrl);
      console.log(respon.data.results);
      setMovies(respon.data.results);
    };
    fetchData();
  }, [fetchUrl]);

  return (
    <div className="row">
      <div className="row__header">
        <p className="row__header-title">{title}</p>
        {/* <div className="row__header-icon">
          <i class="bx bx-chevron-left"></i>
          <i class="bx bx-chevron-right"></i>
        </div> */}
      </div>
      <div className="row__posters">
        <Slider {...settings}>
          {movies.map((movie, index) => (
            <div key={index}>
              <header
                onClick={() => {
                  handleClick(movie);
                }}
                className="row__posters-item"
                style={{
                  backgroundImage: `url("https://image.tmdb.org/t/p/original/${movie.poster_path}")`,
                  backgroundSize: "cover",
                  backgroundRepeat: "no-repeat",
                  backgroundPosition: "center",
                }}
              ></header>
            </div>
          ))}
        </Slider>
      </div>
      {trailerUrl && <YouTube videoId={trailerUrl} opts={opts} />}
    </div>
  );
};
