import React, { useEffect, useState } from "react";
import "../scss/SidebarRightItem.scss";
import axios from "axios";

export const SidebarRightItem = ({ title, fetchUrl }) => {
  const [movies, setMovies] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      const respon = await axios.get(fetchUrl);
      const random1 = Math.floor(Math.random() * respon.data.results.length);
      const random2 = Math.floor(Math.random() * respon.data.results.length);
      const random3 = Math.floor(Math.random() * respon.data.results.length);
      setMovies([
        respon.data.results[random1],
        respon.data.results[random2],
        respon.data.results[random3],
      ]);
    };
    fetchData();
  }, [fetchUrl]);
  return (
    <div className="sidebarRightItem">
      <p className="sidebarRightItem__title">{title}</p>
      <div className="sidebarRightItem__content">
        {movies.map((item, index) => (
          <div className="content__item" key={index}>
            <img
              src={`https://image.tmdb.org/t/p/original/${item.poster_path}`}
              alt=""
              className="content__item-img"
            />
            <div className="content__item-info">
              <p>{item?.title || item?.name || item?.original_name}</p>
              <div className="content__item-info-div">
                <button>IMDb</button>
                <span>6.9</span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};
